.range-section {
  grid-area: b;
  display: flex;
  flex-direction: column;
  row-gap: 30px;
  padding: 15px;
  background-color: rgba(36, 73, 70, 0.75);
  border-radius: 10px;
  backdrop-filter: blur(5px);
}

.range-title {
  width: 100%;
  font-size: 20px;
  font-weight: 300;
  text-transform: uppercase;
}

.range-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.count-container,
.year-container {
  display: flex;
  flex-wrap: wrap;
  row-gap: 15px;
}

.count-subtitle,
.year-subtitle {
  width: 100%;
}

.count-label,
.year-label {
  display: flex;
  width: 100%;
  column-gap: 10px;
  justify-content: space-between;
}

.count-input,
.year-input {
  width: 100%;
}

.count-input-container,
.year-input-container {
  position: relative;
  pointer-events: none;
  width: 100%;
}

.count-input-container input[type='range'],
.year-input-container input[type='range'] {
  height: 2em;
  display: block;
  width: 100%;
  margin: 0;
}

.count-input-container input[type='range']:last-child,
.year-input-container input[type='range']:last-child {
  margin-top: -2em;
}

input[type='range']::-webkit-slider-thumb {
  pointer-events: all;
}
input[type='range']::-moz-range-thumb {
  pointer-events: all;
}
input[type='range']::-ms-thumb {
  pointer-events: all;
}

.count-min,
.count-max,
.year-min,
.year-max {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 60px;
  height: 30px;
  border: 1px solid skyblue;
  border-radius: 20px;
  background: radial-gradient(118.88% 606.86% at 0% 0%, rgba(36, 197, 219, 0.15) 0%, rgba(36, 197, 219, 0) 100%);
}
