.popup-wrapper {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.popup-wrapper.active {
  cursor: pointer;
  opacity: 1;
}

.popup {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 200px;
  padding: 15px;
  background-color: rgba(36, 73, 70, 0.75);
  border-radius: 10px;
  backdrop-filter: blur(5px);
  transform: translateY(-200px);
  opacity: 0;
}
.popup.active {
  transform: translateY(0);
  opacity: 1;
}
.popup-exit-button {
  cursor: pointer;
  position: relative;
  display: block;
  width: 30px;
  height: 30px;
}
.popup-exit-button::before {
  content: '';
  position: absolute;
  display: block;
  top: calc(50% - 2px);
  width: 100%;
  height: 4px;
  transform: rotate(45deg);
  background-color: white;
  transition: background ease 150ms;
}
.popup-exit-button::after {
  content: '';
  position: absolute;
  display: block;
  top: calc(50% - 2px);
  width: 100%;
  height: 4px;
  transform: rotate(-45deg);
  background-color: white;
  transition: background-color ease 150ms;
}
.popup > .exit-button:hover::after,
.popup > .exit-button:hover::before {
  background-color: skyblue;
}
