.sort-section {
  grid-area: c;
  display: flex;
  flex-direction: column;
  row-gap: 30px;
  padding: 15px;
  background-color: rgba(36, 73, 70, 0.75);
  border-radius: 10px;
  backdrop-filter: blur(5px);
}

.sort-title {
  font-size: 20px;
  font-weight: 300;
  text-transform: uppercase;
}

.sort-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.sort-select {
  padding: 5px 10px;
  border: 1px solid skyblue;
  border-radius: 15px;
  background: radial-gradient(118.88% 606.86% at 0% 0%, rgba(36, 197, 219, 0.15) 0%, rgba(36, 197, 219, 0) 100%);
}
.sort-option {
  color: black;
}
.reset-button {
  padding: 5px 10px;
  border-radius: 15px;
  background: radial-gradient(118.88% 606.86% at 0% 0%, rgba(36, 197, 219, 0.15) 0%, rgba(36, 197, 219, 0) 100%);
  border: 1px solid skyblue;
  transition: all ease 150ms;
}
.reset-button:hover {
  background: radial-gradient(118.88% 606.86% at 0% 0%, rgba(186, 232, 238, 0.15) 0%, rrgba(3, 215, 243, 0) 100%);
}
