.footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 60px;
  background-color: #192f2d;
}

.rss-logo {
  width: 150px;
  height: 50px;
  background-image: url(../../assets/svg/rss.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.author-container {
  display: flex;
  align-items: center;
  column-gap: 20px;
}
