.value-filter {
  grid-area: a;
  display: flex;
  flex-direction: column;
  row-gap: 30px;
  padding: 15px;
  background-color: rgba(36, 73, 70, 0.75);
  border-radius: 10px;
  backdrop-filter: blur(5px);
}

.value-title {
  font-size: 20px;
  font-weight: 300;
  text-transform: uppercase;
}

.value-list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.value-item {
  display: flex;
  align-items: center;
  column-gap: 10px;
}

.toyform-list,
.color-list,
.size-list {
  display: flex;
  align-items: center;
  column-gap: 10px;
}

.toyform-item {
  display: inline-block;
  width: 40px;
  height: 40px;
  background-size: cover;
  transition: all ease 200ms;
  transform: scale(0.8);
  border-radius: 50%;
}
.toyform-item.toyform-ball {
  background-image: url(../../assets/svg/ball.svg);
}
.toyform-item.toyform-bell {
  background-image: url(../../assets/svg/bell.svg);
}
.toyform-item.toyform-cone {
  background-image: url(../../assets/svg/cone.svg);
}
.toyform-item.toyform-snowflake {
  background-image: url(../../assets/svg/snowflake.svg);
}
.toyform-item.toyform-toy {
  background-image: url(../../assets/svg/toy.svg);
}

.color-item {
  position: relative;
  display: inline-block;
  width: 35px;
  height: 35px;
  border-radius: 5px;
  transition: all ease 200ms;
  transform: scale(0.8);
}

.size-item {
  display: inline-block;
  background-image: url(../../assets/svg/ball.svg);
  background-size: cover;
  transition: all ease 200ms;
  transform: scale(0.8);
  border-radius: 50%;
}
.size-item.big {
  width: 40px;
  height: 40px;
}
.size-item.medium {
  width: 32px;
  height: 32px;
}
.size-item.small {
  width: 24px;
  height: 24px;
}

.favorites-checkbox {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 30px;
  border: 1px solid skyblue;
  border-radius: 5px;
  transition: all ease 200ms;
}

.toyform-item:hover,
.color-item:hover,
.size-item:hover,
.favorites-checkbox:hover {
  cursor: pointer;
  -webkit-box-shadow: 0px 0px 24px 7px rgba(51, 114, 109, 1);
  -moz-box-shadow: 0px 0px 24px 7px rgba(51, 114, 109, 1);
  box-shadow: 0px 0px 24px 7px rgba(51, 114, 109, 1);
}

.toyform-item.active,
.size-item.active {
  transform: scale(1);
  filter: invert(9%) sepia(63%) saturate(461%) hue-rotate(322deg) brightness(83%) contrast(88%);
}
.favorites-checkbox.active::after {
  content: '';
  display: block;
  position: absolute;
  top: 6px;
  left: 10px;
  width: 6px;
  height: 10px;
  border: solid #000000;
  border-width: 0 4px 4px 0;
  transform: rotate(45deg);
}
.color-item.active::after {
  content: '';
  display: block;
  position: absolute;
  top: 9px;
  left: 12px;
  width: 6px;
  height: 10px;
  border: solid #000000;
  border-width: 0 4px 4px 0;
  transform: rotate(45deg);
}
.favorites-checkbox.active {
  background-color: skyblue;
}
