.toys-container {
  grid-area: d;
  position: relative;
  display: flex;
  overflow: scroll;
  width: 100%;
}

.toys {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(256px, 1fr));
  gap: 20px;
  width: 100%;
}

.toy {
  cursor: pointer;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  row-gap: 20px;
  height: 260px;
  padding: 15px;
  background-color: rgba(36, 73, 70, 0.75);
  border-radius: 10px;
  backdrop-filter: blur(5px);
  transition: all ease 150ms;
  transform: scale(0.98);
}
.toy.active {
  border: 1px solid #cbb77a;
  transform: scale(1);
}
.toy.active .toy-favorite {
  background-color: #cbb77a;
}

.toy-title {
  width: 100%;
  font-size: 18px;
  font-weight: 400;
}

.toy-content {
  display: flex;
  justify-content: space-between;
  flex: 1 1 auto;
  column-gap: 15px;
}

.img-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1 1 auto;
  padding: 10px 0;
}

.toy-image {
  height: 100px;
  object-fit: contain;
}

.toy-favorite {
  align-self: flex-start;
  width: 80px;
  height: 30px;
  background-color: #24c5db;
  transform: translateX(-15px);
}

.description-list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 14px;
}
