.toys-section {
  display: grid;
  grid-template-areas:
    'a b c'
    'd d d';
  grid-template-rows: 280px 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px;
  width: 100%;
  height: calc(100vh - 130px);
  padding: 20px 60px 0;
}
@media (max-width: 1100px) {
  .toys-section {
    grid-template-areas:
      'a d d'
      'b d d'
      'c d d';
  }
}
