.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-height: 70px;
  padding: 0 60px;
  background-color: #192f2d;
}

.nav-list,
.search-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  column-gap: 20px;
}

.nav-button {
  padding: 0 3px;
}

.nav-button.icon {
  width: 60px;
  height: 60px;
  background-image: url(../../assets/svg/tree.svg);
  background-size: cover;
}

.reset-input-button {
  cursor: pointer;
  position: relative;
  display: block;
  width: 30px;
  height: 30px;
}
.reset-input-button::before {
  content: '';
  position: absolute;
  display: block;
  top: calc(50% - 2px);
  width: 100%;
  height: 4px;
  transform: rotate(45deg);
  background-color: white;
  transition: background ease 150ms;
}
.reset-input-button::after {
  content: '';
  position: absolute;
  display: block;
  top: calc(50% - 2px);
  width: 100%;
  height: 4px;
  transform: rotate(-45deg);
  background-color: white;
  transition: background-color ease 150ms;
}
.reset-input-button:hover::after,
.reset-input-button:hover::before {
  background-color: skyblue;
}

.search-input-wrapper {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 254px;
  padding: 9px;
  border: 1px solid skyblue;
  border-radius: 20px;
  background: radial-gradient(118.88% 606.86% at 0% 0%, rgba(36, 197, 219, 0.15) 0%, rgba(36, 197, 219, 0) 100%);
}
.search-input-wrapper::before {
  content: '';
  position: absolute;
  top: 50%;
  right: 0;
  width: 25px;
  height: 25px;
  transform: translate(-50%, -50%);
  background-image: url(../../assets/svg/search.svg);
  background-repeat: no-repeat;
  background-size: contain;
}
.search-input {
  cursor: pointer;
  padding: 0 40px 0 5px;
  background-color: transparent;
  border: none;
}
